.talkBox dt{height:22px;line-height: 22px;}
.talkBox dt a{color:#000;font-size:14px;font-weight: bold}
.talkBox dt,.talkBox dd{text-align:left;position: relative;}
.talkBox dt a,.talkBox dd a{display:inline-block;height:22px;letter-spacing:-0.5px}
.talkBox dd{margin-top:8px}
.talkBox dd ul{overflow: hidden}
.talkBox dd li{float:left;  margin-left:48px;height:22px;line-height: 22px;margin-bottom:4px}
.talkBox dd li:first-child{margin-left:0}
.talkBox dd li em{font-style: normal;color:#1fbc02}
.talkBox dd li:first-child + li + li a em{color:#999}
.talkBox #chk_agree a:hover{text-decoration:underline}
.talkBox dl button.on{display:inline-block;float:left;border:0;width:22px;height:22px;background:url('#{$imgUrl-center}/popup/pop_chkd2.png') no-repeat;margin-right:6px;}
.talkBox .go_register{width:460px;height:60px;margin:124px auto 0}
.talkBox .go_register a{display:block;height:60px}
.talkBox .btn_clse{position: absolute;width:80px;height:80px;right:0;top:0}
.talkBox .btn_clse a{display:block;height:80px}

