.midBody .btn_cfm a{colro:#fff;display:inline-block;width:166px;height:52px;color:#fff}
.midBody .btn_cfm a:hover{text-decoration: none}
.midBody .btmFooter{border-top:1px solid #dddfe0;padding:20px 0 50px}
