/* 후스콜 콜카드 팝업 */
.pop_cont_wrap{position:absolute;left:50%;top:0;width:820px;}
.pop_cont_head{width:820px;height:582px;background:url('#{$imgUrl-center}/popup/pop_whos_call.png') no-repeat 0 0;}
.pop_cont_wrap .apply_form_wrap{height:153px;padding:25px 128px 0 128px;background-color: #f4f4f6;}
.apply_info dt{float:left;width:140px;font-family:'돋움', Dotum, AppleGothic, Sans-serif;font-size:14px;font-weight:bold;color:#181818;line-height:47px;}
.apply_info dd{position:relative;float:left;padding:5px 0 5px 0;}
.apply_info dd input{width:394px;height:35px;padding:0 14px;border:1px solid #d8d8da;color:#181818;font-family:'돋움', Dotum, AppleGothic, Sans-serif;font-size:12px;border-radius:2px;line-height:35px;}
.apply_info dd input:-ms-input-placeholder{color:#bbb;font-size:12px;line-height:35px;}
.guide_txt{position:absolute;left:14px;top:6px;color:#bbb;font-size:12px;line-height:35px;}
.pop_notice{clear:both;margin-left:140px;padding-top:7px;#{$ie7-hack}padding-top:2px}
.pop_notice .ic_notice{content:'';display:block;float:left;width:16px;height:16px;background:url('#{$imgUrl-center}/popup/pop_whos_call.png') no-repeat -414px -584px;}
.pop_notice .txt{display:block;padding-left:24px;font-size:11px;color:#929292;line-height:16px}
.agreement_wrap{padding:21px 128px 0 128px;}
.agree_all_sec{position:relative;padding:11px 0 11px 34px;font-size:14px;font-weight:bold;color:#181818;border-bottom:1px solid #eeeef0}
.agree_all_sec input[type=checkbox], .agree_lst input[type=checkbox]{width:0;height:0;font-size:0;visibility:hidden;}
.agree_all_sec .chk_box, .agree_lst .chk_box{content: '';position:absolute;left:0;top:17px;margin-top:-12px;#{$ie7-hack}margin-top:-15px;width:24px;height:24px;margin-right: 4px;background: url(#{$imgUrl-center}/popup/pop_whos_call.png) no-repeat -362px -584px;}
.agree_all_sec .chk_box{top:20px;}
.agree_all_sec.checked .chk_box, .agree_lst li.checked .chk_box{background-position:-388px -584px}
.agree_lst{padding-top:3px;}
.agree_lst li{position:relative;padding:8px 0 8px 34px;position:relative;line-height:18px;font-size:12px;color:#181818;}
.agree_lst li label{font-weight: bold;}
.agree_lst li em{padding-left:5px;color:#17c900}
.agree_lst li .btn_detail{position:absolute;right:0;font-size:12px;color:#a0a0a0;text-decoration:underline}
.agree_lst li.last .btn_detail{bottom:8px;}
.agreement_wrap .btn_join{display:block;width:360px;height:54px;margin:22px auto 25px auto;background:url('#{$imgUrl-center}/popup/pop_whos_call.png') no-repeat 0 -584px;white-space:nowrap;text-indent:100%;overflow:hidden}
.pop_cont_wrap .btn_clse{position:absolute;right:20px;top:20px;width:37px;height:37px;border:0;background-color: transparent;white-space:nowrap;text-indent:100%;overflow:hidden}